.sky {
  background: linear-gradient(#85a5cc 0, #d0e4f2 100%);
  box-shadow: 0 0 20px #d0e4f2;
  height: 44%;
  overflow: hidden;
  position: absolute;
  width: 100%; }
  .sky .cloud {
    background: #ebf5fc;
    border-radius: 200px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    height: 39px;
    margin-right: -102px;
    position: absolute;
    right: 0%;
    width: 102px; }
    .sky .cloud.variant-1 {
      top: 78%;
      -webkit-transform: scale(0.2);
      -webkit-animation: clouds-in-out 16s 0s infinite linear; }
    .sky .cloud.variant-2 {
      top: 59%;
      -webkit-transform: scale(0.4);
      -webkit-animation: clouds-in-out 17s 2s infinite linear; }
    .sky .cloud.variant-3 {
      top: 36%;
      -webkit-transform: scale(0.6);
      -webkit-animation: clouds-in-out 18s 4s infinite linear; }
    .sky .cloud.variant-4 {
      top: 51%;
      -webkit-transform: scale(0.8);
      -webkit-animation: clouds-in-out 19s 6s infinite linear; }
    .sky .cloud.variant-5 {
      top: 38%;
      -webkit-transform: scale(1);
      -webkit-animation: clouds-in-out 20s 8s infinite linear; }
    .sky .cloud:before, .sky .cloud:after {
      background: #ebf5fc;
      border-radius: 100px;
      content: '';
      height: 45px;
      left: 10px;
      position: absolute;
      top: -10px;
      width: 60px;
      -webkit-transform: rotate(30deg);
      -moz-transform: rotate(30deg);
      transform: rotate(30deg); }
    .sky .cloud:after {
      height: 60px;
      left: auto;
      right: 12px;
      top: -24px;
      width: 60px; }

.ground {
  background: linear-gradient(#6e9e03 0, #4f7302 100%);
  height: 57%;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0; }

@-webkit-keyframes rainbow-in-out {
  0% {
    -webkit-transform: rotate(180deg); }
  33% {
    -webkit-transform: rotate(360deg); }
  66% {
    -webkit-transform: rotate(360deg); }
  100% {
    -webkit-transform: rotate(540deg); } }

@-webkit-keyframes shadow-in-out {
  0% {
    opacity: 0;
    width: 0; }
  33% {
    opacity: 1;
    width: 60px; }
  66% {
    opacity: 1;
    width: 60px; }
  100% {
    opacity: 0;
    width: 0; } }

@-webkit-keyframes clouds-in-out {
  0% {
    right: 0%; }
  100% {
    right: 105%; } }
